import React from 'react';
import { Route} from 'react-router-dom';
import loadable from 'react-loadable';
import Header from 'components/Layout/Header';
import Sidenav from 'components/Layout/Sidenav';
import Footer from 'components/Layout/Footer';
import LoadingComponent from 'components/Loading';
import { loadProgressBar } from 'axios-progress-bar';
import 'axios-progress-bar/dist/nprogress.css';

let Dashboard = loadable({
  loader: () => import('routes/app/routes/dashboard/'),
  loading: LoadingComponent
})
let Chart = loadable({
  loader: () => import('routes/app/routes/chart/'),
  loading: LoadingComponent
})
let ECommerce = loadable({
  loader: () => import('routes/app/routes/ecommerce/'),
  loading: LoadingComponent
})
let Form = loadable({
  loader: () => import('routes/app/routes/form/'),
  loading: LoadingComponent
})
let Page = loadable({
  loader: () => import('routes/app/routes/page/'),
  loading: LoadingComponent
})
let PageLayout = loadable({
  loader: () => import('routes/app/routes/page-layout/'),
  loading: LoadingComponent
})
let Table = loadable({
  loader: () => import('routes/app/routes/table/'),
  loading: LoadingComponent
})
let UI = loadable({
  loader: () => import('routes/app/routes/ui/'),
  loading: LoadingComponent
})
let Exception = loadable({
  loader: () => import('routes/exception/'),
  loading: LoadingComponent
})
let Category = loadable({
    loader: () => import('routes/admin/routes/product/category/'),
    loading: LoadingComponent
})
let CategoryAdd = loadable({
    loader: () => import('routes/admin/routes/product/categoryadd/'),
    loading: LoadingComponent
})
let CategoryEdit = loadable({
    loader: () => import('routes/admin/routes/product/categoryedit/'),
    loading: LoadingComponent
})
let SlideTop = loadable({
  loader: () => import('routes/admin/routes/slide/slidetop/'),
  loading: LoadingComponent
})
let SlideTopAdd = loadable({
  loader: () => import('routes/admin/routes/slide/slidetopadd/'),
  loading: LoadingComponent
})
let SlideTopEdit = loadable({
  loader: () => import('routes/admin/routes/slide/slidetopedit/'),
  loading: LoadingComponent
})
let SlideBottom = loadable({
  loader: () => import('routes/admin/routes/slide/slidebottom/'),
  loading: LoadingComponent
})
let SlideBottomAdd = loadable({
  loader: () => import('routes/admin/routes/slide/slidebottomadd/'),
  loading: LoadingComponent
})
let SlideBottomEdit = loadable({
  loader: () => import('routes/admin/routes/slide/slidebottomedit/'),
  loading: LoadingComponent
})
let Brand = loadable({
  loader: () => import('routes/admin/routes/product/brand/'),
  loading: LoadingComponent
})
let BrandAdd = loadable({
  loader: () => import('routes/admin/routes/product/brandadd/'),
  loading: LoadingComponent
})
let BrandEdit = loadable({
  loader: () => import('routes/admin/routes/product/brandedit/'),
  loading: LoadingComponent
})


class MainAdmin extends React.Component {
  constructor(props){
    super(props);
    this.state = {

    }
    loadProgressBar();
  }
  render() {
    const { match } = this.props;
    return (
      <div className="main-app-container">
        <Sidenav />

        <section id="page-container" className="app-page-container">
          <Header />

          <div className="app-content-wrapper">
            <div className="app-content">
              <div className="h-100">
                <Route path={`${match.url}/category`} component={Category} />
                <Route path={`${match.url}/categoryadd`} component={CategoryAdd} />
                <Route path={`${match.url}/categoryedit/:id`} component={CategoryEdit} />
                <Route path={`${match.url}/brand`} component={Brand} />
                <Route path={`${match.url}/brandadd`} component={BrandAdd} />
                <Route path={`${match.url}/brandedit/:id`} component={BrandEdit} />
                <Route path={`${match.url}/slidetop`} component={SlideTop} />
                <Route path={`${match.url}/slidetopadd`} component={SlideTopAdd} />
                <Route path={`${match.url}/slidetopedit/:id`} component={SlideTopEdit} />
                <Route path={`${match.url}/slidebottom`} component={SlideBottom} />
                <Route path={`${match.url}/slidebottomadd`} component={SlideBottomAdd} />
                <Route path={`${match.url}/slidebottomedit/:id`} component={SlideBottomEdit} />
                {/* <Route path={`${match.url}/categoryedit/:id`} component={CategoryEdit} /> */}
                <Route path={`${match.url}/dashboard`} component={Dashboard} />
                <Route path={`${match.url}/chart`} component={Chart} />
                <Route path={`${match.url}/ecommerce`} component={ECommerce} />
                <Route path={`${match.url}/form`} component={Form} />
                <Route path={`${match.url}/page`} component={Page} />
                <Route path={`${match.url}/pglayout`} component={PageLayout} />
                <Route path={`${match.url}/table`} component={Table} />
                <Route path={`${match.url}/ui`} component={UI} />
                <Route path={`${match.url}/exception`} component={Exception} />
              </div>
            </div>

            <Footer />
          </div>
        </section>

      </div>
    );
  }
}

export default MainAdmin;
